"use client";

import { useState } from "react";

import { SectionCard } from "@/components/dashboard/section-card";
import { TimeSeriesChart } from "@/components/dashboard/time-series-chart";
import { DonutCategoryChart } from "@/components/dashboard/donut-category-chart";
import { HistogramChart } from "@/components/dashboard/histogram-chart";
import { JointDistributionChart } from "@/components/dashboard/joint-distribution-chart";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import type { ComercialDashboardData } from "@/data/comercial";
import type { TicketHistogram } from "@/lib/analytics-client";

interface ComercialDashboardClientProps {
  data: ComercialDashboardData;
  distTicket: TicketHistogram | null;
}

type ComercialTab = "resumo" | "tickets" | "canais";

export function ComercialDashboardClient({
  data,
  distTicket,
}: ComercialDashboardClientProps) {
  const [activeTab, setActiveTab] = useState<ComercialTab>("resumo");

  return (
    <div className="space-y-6">
      <div className="flex flex-wrap gap-2 border-b border-border pb-2 text-xs">
        <button
          type="button"
          onClick={() => setActiveTab("resumo")}
          className={`rounded-full px-3 py-1 transition-colors ${
            activeTab === "resumo"
              ? "bg-primary text-primary-foreground"
              : "bg-secondary text-secondary-foreground hover:bg-secondary/80"
          }`}
        >
          Resumo
        </button>
        <button
          type="button"
          onClick={() => setActiveTab("tickets")}
          className={`rounded-full px-3 py-1 transition-colors ${
            activeTab === "tickets"
              ? "bg-primary text-primary-foreground"
              : "bg-secondary text-secondary-foreground hover:bg-secondary/80"
          }`}
        >
          Tickets e funil
        </button>
        <button
          type="button"
          onClick={() => setActiveTab("canais")}
          className={`rounded-full px-3 py-1 transition-colors ${
            activeTab === "canais"
              ? "bg-primary text-primary-foreground"
              : "bg-secondary text-secondary-foreground hover:bg-secondary/80"
          }`}
        >
          Canais
        </button>
      </div>

      {activeTab === "resumo" ? (
        <section className="grid gap-4 grid-cols-1 md:grid-cols-2">
          <SectionCard
            title="Vendas por mês"
            subtitle="Tendência de faturamento"
          >
            <TimeSeriesChart
              data={data.vendasPorMes.map((p) => ({
                label: new Date(p.date).toLocaleDateString("pt-BR", {
                  month: "short",
                }),
                value: p.value,
              }))}
              yLabel="Faturamento mensal"
            />
          </SectionCard>

          <SectionCard
            title="Vendas por canal"
            subtitle="Distribuição da receita por canal"
          >
            <DonutCategoryChart
              data={data.vendasPorCanal.map((canal) => ({
                label: canal.canal,
                value: canal.valor,
              }))}
            />
          </SectionCard>

          <SectionCard
            title="Principais clientes"
            subtitle="Top contas por faturamento"
          >
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Cliente</TableHead>
                  <TableHead>Faturamento</TableHead>
                  <TableHead>Margem</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {data.principaisClientes.map((cliente) => (
                  <TableRow key={cliente.id}>
                    <TableCell>{cliente.nome}</TableCell>
                    <TableCell>
                      {cliente.faturamentoMensal.toLocaleString("pt-BR", {
                        style: "currency",
                        currency: "BRL",
                        maximumFractionDigits: 0,
                      })}
                    </TableCell>
                    <TableCell>
                      {cliente.margemPercentual.toFixed(1)}%
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </SectionCard>
        </section>
      ) : null}

      {activeTab === "tickets" ? (
        <section className="grid gap-4 grid-cols-1 md:grid-cols-2">
          <SectionCard
            title="Distribuição de tickets"
            subtitle="Histograma por faixa de valor de pedido"
          >
            <HistogramChart
              values={
                distTicket?.values?.length
                  ? distTicket.values
                  : data.distribuicaoTicket ?? []
              }
              bins={distTicket?.bins}
              density={distTicket?.density}
              stats={distTicket?.stats}
              unit="R$"
            />
          </SectionCard>

          <SectionCard
            title="Funil de vendas"
            subtitle="Conversão por etapa e valor em pipeline"
          >
            <HistogramChart
              values={data.distribuicaoTicket}
              unit="R$"
            />
          </SectionCard>
        </section>
      ) : null}

      {activeTab === "canais" ? (
        <section className="space-y-4">
          <SectionCard
            title="Joint distribution clientes"
            subtitle="Faturamento mensal × margem percentual"
          >
            <JointDistributionChart
              points={data.principaisClientes.map((c) => ({
                x: c.faturamentoMensal,
                y: c.margemPercentual,
                label: c.nome,
              }))}
              xLabel="Faturamento mensal (R$)"
              yLabel="Margem (%)"
            />
          </SectionCard>

          <SectionCard
            title="Receita mensal por canal"
            subtitle="Comparando a evolução de cada canal"
          >
            <div className="grid gap-4 grid-cols-1 md:grid-cols-2">
              {Array.from(
                new Map(
                  data.vendasCanalMes.map((item) => [
                    item.canal,
                    data.vendasCanalMes.filter((i) => i.canal === item.canal),
                  ]),
                ).entries(),
              ).map(([canal, pontos]) => (
                <SectionCard
                  key={canal}
                  title={canal}
                  subtitle="Evolução mensal"
                >
                  <TimeSeriesChart
                    data={(pontos as typeof data.vendasCanalMes).map((p) => ({
                      label: p.mes,
                      value: p.valor,
                    }))}
                    yLabel="Receita (R$)"
                  />
                </SectionCard>
              ))}
            </div>
          </SectionCard>
        </section>
      ) : null}
    </div>
  );
}


